<script>
  import { onMount } from "svelte";
  import { createEventDispatcher } from "svelte";

  let currentScreenWidth;
  let canRender = false;
  let logoSize = 20;
  let isButtonHovered = false;
  let isDrawerToggled = false;
  let isDrawerOpened = false;
  let isIntervalStarted = false;
  let navbarHidden = false;
  let navbarShowing = false;
  let currentY;
  let drawerBackground;
  const dispatch = createEventDispatcher();

  function setupWidget() {
    currentScreenWidth = window.screen.width;
    console.log("mounted " + currentScreenWidth);
    if (currentScreenWidth >= 1920) {
      logoSize = 30;
    } else if (currentScreenWidth >= 768) {
      logoSize = 25;
    } else if (currentScreenWidth >= 375) {
      logoSize = 20;
    }
    canRender = true;
    console.log("current logo size: " + logoSize);
  }

  function onDrawerClicked() {
    isDrawerToggled = !isDrawerToggled;
    if (isDrawerToggled) {
      isIntervalStarted = true;
      setTimeout(() => {
        isIntervalStarted = false;
        isDrawerOpened = true;
        dispatch("showMenu", { open: isDrawerToggled });
      }, 500);
    } else {
      isDrawerOpened = false;
      dispatch("showMenu", { open: isDrawerToggled });
    }
  }

  function updateNavbarVisibility(visibile) {
    if (visibile) {
      navbarHidden = false;
      navbarShowing = true;
    } else {
      navbarHidden = true;
      navbarShowing = false;
    }
  }
  let prevScroll = 0;
  function setNavbarVisibility(e) {
    if (!("ontouchstart" in window)) {
      console.log("touch not detected");
      if (!isDrawerToggled) {
        if (e.deltaY < 0) {
          //scrolling up
          console.log("su");
          updateNavbarVisibility(true);
        } else {
          //scrolling down
          console.log("giù");
          updateNavbarVisibility(false);
        }
      }
    } else {
      console.log("touch detected");
      let delta = prevScroll - e.changedTouches[0].clientY;
      console.log(delta);
      if (delta < 0) {
        updateNavbarVisibility(true);
      } else {
        updateNavbarVisibility(false);
      }
      prevScroll = e.changedTouches[0].clientY;
    }
  }

  onMount(setupWidget);

  window.addEventListener("resize", setupWidget);
  window.addEventListener("wheel", setNavbarVisibility);
  window.addEventListener("touchmove", setNavbarVisibility);
</script>

{#if canRender}
  <div class="fixed bg-transparent w-full py-32 px-30 sm:px-60 md:px-90">
    <div
      class="flex align-center justify-between w-full max-w-full transition-all duration-200 
      {navbarHidden ? 'h-0 opacity-0' : ''} 
        {navbarShowing ? 'h-full opacity-100' : ''}"
    >
      <a class="prevent-selection flex justify-center align-center" href="/">
        <img
          src="/assets/logo{logoSize}.svg"
          height="{logoSize}px"
          class="w-full"
          alt=""
        />
      </a>
      <div class="relative">
        <div
          class="flex align-center justify-center rounded-full 
          transition-all duration-200 
          w-58 sm:w-70 md:w-76
          h-58 sm:h-70 md:h-76
        {isDrawerOpened ? 'bg-blue' : 'bg-red'}
         {isButtonHovered ? 'animate-scale-in-bounce' : 'animate-scale-out'}
        "
          on:mouseenter={() => (isButtonHovered = true)}
          on:mouseleave={() => (isButtonHovered = false)}
        />
        <div
          class="absolute top-1/2 left-1/2 transform transition-all duration 200 -translate-x-1/2 -translate-y-1/2
          bg-transparent border-white my-auto
          {isDrawerToggled
            ? 'w-32 sm:w-38 md:w-42 h-32 sm:h-38 md:h-42'
            : 'w-38 sm:w-46 md:w-52 h-38 sm:h-46 md:h-52'}
          border-3.5 sm:border-4 md:border-4.5"
          style="
                 border-radius: {isDrawerToggled ? '0%' : '50%'}"
          on:mouseenter={() => (isButtonHovered = true)}
          on:mouseleave={() => (isButtonHovered = false)}
          on:click={onDrawerClicked}
        />
      </div>
    </div>
  </div>
{/if}

<style>
</style>
