<script>
  import Header from "./Header.svelte";
  import Landing from "./Landing.svelte";
  import Menu from "./Menu.svelte";
  import Contacts from "./Contacts.svelte";
  import { Router, Route, Link } from "svelte-navigator";
  import Footer from "./Footer.svelte";

  let showMenu = false;
  let navColor = "transparent";

  function onShowMenu(e) {
    showMenu = e.detail.open;
    if (showMenu) {
      //navColor = "#1c0e3f";
      navColor = "transparent";
    } else {
      navColor = "transparent";
    }
  }
</script>

<main>
  <Router url="">
    <div class="font-display w-full min-h-screen">
      <Header on:showMenu={onShowMenu} />
      {#if showMenu}
        <Menu />
      {:else}
        <Route path="/">
          <Landing />
        </Route>
        <Route path="/contacts">
          <Contacts />
        </Route>
      {/if}
      <footer>
        <Footer />
      </footer>
    </div>
  </Router>
</main>

<style global lang="postcss">
  @tailwind base;

  @layer base {
    a:visited {
      text-color: white;
    }
  }
  @tailwind components;

  @tailwind utilities;
  main {
    display: flex;
  }
</style>
